
export default {
	data:{
		userInfo:{}
	},
	
	onLaunch: function () {
		console.log('App Launch')
	},
	onShow: function () {
		console.log('App Show')
	},
	onHide: function () {
		console.log('App Hide')
	},
	checkStrNotNull(str) {
		if (str == '' || str == undefined || str == null){
			return false;
		}
		return true;
	},
	serverUrl:'http://192.168.1.7:8081',
	imageServerUrl:'http://47.99.243.19:88',
	getGlobalUserInfo: function () {
		var key = 'userInfo';
		if (key.length === 0) {
			uni.showModal({
				title: '',
				content: "key ",
				showCancel:false
			})
		} else {
			return JSON.parse(uni.getStorageSync(key));
		}
	},
	setGlobalUserInfo: function (userInfo) {
		var key = "userInfo"
		var data = JSON.stringify(userInfo);
		if (key.length === 0) {
			uni.showModal({
				title: '',
				content: 'key ',
				showCancel:false
			})
		} else {
			uni.setStorage({
				key: key,
				data: data,
				success() {
					
				},
				fail: () => {
					uni.showModal({
						title: '!',
						showCancel:false
					})
				}
			})
		}
	},
	clearStorage: function () {
		uni.clearStorageSync()
		
		uni.showModal({
			title: '',
			content: ' ',
			showCancel:false
		})
	}
}



